		return {
			// background: ['color1', 'color2', 'color3'],
			indicatorDots: false,
			autoplay: false,
			circular: true,
			interval: 2000,
			duration: 500,
			currentTab: 0,
			height: 100,
			titlelist: [1, 2, 3, 4, 5, 6, 7, 8]
		};
